    export default {
        data() {
            return {
                showtime: false,   // 显示自定义事件弹窗
                all: false,    // 详情弹窗
                startTime: "",  //  开始时间
                entTime: "",  //  结束时间
                start: null,
                end: null,
                page: 1,            //  呼叫列表请求页码页数
                maxPage: 1,
                limit: 10,     //  一次限制请求几条数据
                onFetching: false,
                loadText: '正在拼命加载中',        //  呼叫中列表  下拉提示词
                loading: true,                  //   呼叫中列表   下拉圈圈
                shopName: ["全部","卫生打扫","杯子","换麦克风","纸巾","开水","寄取酒","点单","碗筷","其他"],
                type_id: null,    //  投诉类型ID
                show: false,  // 判断筛选弹窗显示或隐藏
                company_name: "",  // 类型
                value: 0,   //  默认显示昨天
                allNum: [],
//               人员列表
                nameList: [],
            }
        },
        created(){
            this.startTime = this.common.commonFun.get_date(1);
            this.entTime = this.common.commonFun.get_date(1);
            this.getInfo();
        },
        methods:{
            getInfo(){
                if (this.onFetching) {
                    // do nothing
                } else {
                    this.onFetching = true;
                    if(this.page > this.maxPage){
                        this.onFetching = false;
                        return;
                    }
                    this.common.commonFun.ajax({
                        dom: this,
                        url: this.common.apiUrl.getServiceTypeList,
                        type: "post",
                        data: {
                            page: this.page,
                            limit: this.limit,
                            time: this.startTime,
                            times: this.entTime,
                            type_id: this.type_id
                        },
                        success: (response, allResponse) => {
                            // console.log('response',response)
                            this.onFetching = true;
                            this.maxPage = Math.ceil(allResponse.count/this.limit) || 1;
                            if(this.page >= this.maxPage){
                                this.onFetching = false;
                                console.log('滚动结束');
                                this.loadText = "已经到底了哦~";
                                this.loading = false;
                            }
                            this.nameList = this.nameList.concat(response);
                            this.onFetching = false;
                            this.page ++;
                        }
                    });
                }
            },
            //            自定义弹窗=>点击确定
            onConfirm(){
                this.start = this.startTime.replace(/\-/g,'.').substring(5,10);
                this.end = this.entTime.replace(/\-/g,'.').substring(5,10);
                this.$refs.scrollerBottom.reset({top: 0});
                this.getInfo();
            },
            //            点击筛选触发
            navRigth(){
                console.log("我要筛选了")
                this.show = true;
            },
            //            筛选弹窗确定按钮
            choice(){
                this.show = false;
                this.shopName.forEach((item,index)=>{
                    if(this.company_name == item){
                        this.type_id = index
                    }
                });
                if(this.type_id > 6){
                    this.type_id = this.type_id + 1
                }
                this.page = 1;
                this.maxPage = 1;
                this.nameList = [];
                this.$refs.scrollerBottom.reset({top: 0});
                this.getInfo()
            },
            // 控制详情弹窗显示和隐藏
            overSolve(item){
                this.all = !this.all;
                this.allNum = item.desc
            },
            //            选择时间
            date(index){
                this.value = index;
                this.page = 1;
                this.maxPage = 1;
                if(index == 0){
                    this.nameList = [];
                    this.startTime = this.common.commonFun.get_date(1);
                    this.entTime = this.common.commonFun.get_date(1);
                    this.$refs.scrollerBottom.reset({top: 0});
                    this.getInfo();
                }
                if(index == 1){
                    this.nameList = [];
                    this.startTime = this.common.commonFun.get_date(0);
                    this.entTime = this.common.commonFun.get_date(0);
                    this.$refs.scrollerBottom.reset({top: 0});
                    this.getInfo();
                }
                if(index == 2){
                    this.showtime = true;
                }
            }
        }
    }



